<template>
  <div>
    <h2 class="main-title">내 친구 확인하기</h2>
    <hr class="my-4" />

    <!-- 사용자 정보 표시 -->
    <div id="myInfo">
      <p><strong>{{ myData.mnic }}</strong>님 안녕하세요!</p>
      <p><strong>이메일:</strong> {{ myData.memail }}</p>
    </div>
  </div>
  <hr class="my-4" />

  <!-- 회원(친구) 검색 -->
  <div class="search-area">
    <div class="col-auto">
      <label for="friendSearch" class="form-label">
        친구 검색 - 닉네임으로 검색할 수 있습니다
      </label>
    </div>
    <div class="col-auto">
      <div class="input-group">
        <input type="text" v-model="friendSearchQuery" class="form-control" id="friendSearch" required/>
        <button type="submit" class="btn btn-primary btn-primary-search" data-bs-toggle="modal" data-bs-target="#exampleModal" @click="showSearchFriend">
          검색
        </button>
      </div>
    </div>
  </div>


  <!-- 검색된 친구 목록 -->
  <div class="modal fade" id="exampleModal" tabindex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
    <div class="modal-dialog modal-dialog-centered modal-dialog-scrollable">
      <div class="modal-content">
        <div class="modal-header">
          <h1 class="modal-title fs-5" id="exampleModalLabel">검색된 친구 목록</h1>
        </div>
        <div class="modal-body" >
          <div v-for="friend in searchFriend" :key="friend">
            <p v-if="friend === 0">
              검색된 닉네임이 없습니다.
            </p>
            <p v-if="friend.mstatus_mikey === 201">
              - {{ friend.mnic }} - 
            </p>
            <button type="button" class="btn btn-primary" @click="fetchPlusFriend">친구추가</button>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-primary" data-bs-dismiss="modal" aria-label="Close">닫기</button>
        </div>
      </div>
    </div>
  </div>

  <hr class="my-4" />

  <!-- 내친구 목록보기, 요청대기중인 친구보기 버튼 -->
  <button id="friendList" type="button" class="btn btn-primary" @click="showFriendList">나의 친구목록 보기</button>
  <button id="friendWait" type="button" class="btn btn-primary" @click="showFriendWait">요청 대기중인 친구보기</button>
  <button id="friendCall" type="button" class="btn btn-primary" @click="showFriendCall">받은 요청</button>

  <hr class="my-4" />

  <!-- 내친구 목록보기 -->
  <div v-if="friendList">
    <div class="card text-center">
      <div class="card-header">
        <h5 class="card-title">나의 친구목록</h5>
      </div>
      <div class="card-body">
        <p class="card-text" v-for="friend in myFriendList" :key="friend">
          - {{ friend.mnic }} -
        </p>
      </div>
    </div>
  </div>
  <!-- 요청대기중인 친구목록 보기 -->
  <div v-if="friendWait">
    <div class="card text-center">
      <div class="card-header">
        <h5 class="card-title">요청대기중인 친구</h5>
      </div>
      <div class="card-body">
        <p class="card-text" v-for="friend in myFriendWait" :key="friend">
          - {{ friend.mnic }} -
        </p>
      </div>
    </div>
  </div>
  <!-- 요청받은 친구목록 보기 -->
  <div v-if="friendCall">
    <div class="card text-center">
      <div class="card-header">
        <h5 class="card-title">요청대기중인 친구</h5>
      </div>
      <div class="card-body">
        <p class="card-text" v-for="friend in myFriendCall" :key="friend">
          - {{ friend.mnic }} -
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  data() {
    return {
      myFriendList: [],
      myFriendWait: [],
      myFriendCall: [],
      searchFriend: [],
      myData: {},
      friendList: false,
      friendWait: false,
      friendCall: false,
      friendSearchQuery: ''
    };
  },
  computed: {

  },
  mounted() {
    this.fetchMyData();
  },
  methods: {
    // 페이지들어오면 내정보와 친구목록 가져오기
    async fetchMyData() {
      try {
        const response = await axios.post('/JH_C_MyFriend', { mnum: 1001 }); // 임시로 1001번 데이터로 가져오게함 
        this.myData = response.data;
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error fetching MyFriends:', error);
      }

    },
    // 친구목록 버튼 클릭시 수락된 친구 불러옴
    async showFriendList() {
      try {
        const response = await axios.post('/JH_C_MyFriendList', { mnum: this.myData.mnum }); // 임시로 1001번 데이터로 가져오게함
        this.myFriendList = response.data; 
        console.log("showFriendList clicked");
        this.friendList = true;
        this.friendWait = false;
        this.friendCall = false;
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error showing MyFriendList:', error);
      }
    },
    // 요청대기중인 친구 목록 버튼 클릭시 수락요청중인 친구 불러옴
    async showFriendWait() {
      try {
        const response = await axios.post('/JH_C_MyFriendWait', { mnum: this.myData.mnum }); // 임시로 1001번 데이터로 가져오게함
        this.myFriendWait = response.data;  
        console.log("showFriendWait clicked");
        this.friendWait = true;
        this.friendList = false;
        this.friendCall = false;
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error showing MyFriendWait:', error);
      }
    },
    async showFriendCall() {
      try {
        const response = await axios.post('/JH_C_MyFriendCall', { mnum: this.myData.mnum }); // 임시로 1001번 데이터로 가져오게함
        this.myFriendCall = response.data;  
        console.log("showFriendCall clicked");
        this.friendCall = true;
        this.friendList = false;
        this.friendWait = false;
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error showing MyFriendCall:', error);
      }
    },

    async showSearchFriend() {
      try {
        const response = await axios.post('/JH_C_MyFriendSearch', { mnic: this.friendSearchQuery });
        this.searchFriend = response.data;
        console.log("Search", this.friendSearchQuery);
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error showing MyFriendSearch:', error);
      } 
    },
    
    async fetchPlusFriend() {
      try {
        const response = await axios.post('/JH_C_PlusFriend', { 
          mnum: this.mnum,
          mfnum: this.mfnum,
          mfstatus_makey: 700,
          mfstatus_mikey: 701
        });
        console.log('Response:', response.data);
      } catch (error) {
        console.error('Error showing MyFriendSearch:', error);
      }
    }
  }
}
</script>

<style scoped>

.search-area {
  margin-top: 50px;
  margin-bottom: 50px;

}

.btn-primary {
  background-color: #F5835D;
  border-color: #ffe0c7;
}

/* 검색 버튼에만 다른 스타일 */
.btn-primary-search {
  background-color: #F5835D;
  border-color: #F5835D;
  color: #2c2b2b;
}

.btn-primary-search:hover {
  background-color: #ffe0c7;
  border-color: #ffe0c7;
  color: #2c2b2b;
}
</style>
